import InfoBlock from "../InfoBlock/InfoBlock";
import SocialNetworksList from "../SocialNetworksList/SocialNetworksList";
import Section from "../Section/Section";
import WhiteTitle from "../Title/WhiteTitle";

import masterCard from "../../assets/images/paymentSystem/masterCard.jpg";
import liqPay from "../../assets/images/paymentSystem/liqPay.jpg";
import visa from "../../assets/images/paymentSystem/visa.jpg";
import footerImage from "../../assets/images/footer/youngMotherWithBaby.png";
import { ReactComponent as RightIcon } from "../../assets/images/icon/right.svg";

import styles from "./footer.module.scss";

const Footer = () => {
  return (
    <div className={styles.footer}>
      <div className={styles.subscriptionBlockWrapper}>
        <Section>
          <div className={styles.subscriptionBlock}>
            <div className={styles.wrapperForElements}>
              <div className={styles.inputWrapper}>
                <WhiteTitle text="Отримуйте ПРОМОКОДИ ТА ЕКСКЛЮЗИВНІ ПРОПОЗИЦІЇ" />
              </div>
              <div className={styles.inputWrapper}>
                <input className={styles.input} placeholder="Ваш e-mail" />
                <button className={styles.btn}>
                  <RightIcon />
                </button>
              </div>
            </div>
            <img
              className={styles.image}
              src={footerImage}
              alt="Молода матір зі своїм немовлям"
            />
          </div>
        </Section>
      </div>

      <div className={styles.infoBlockWrapper}>
        <address className={styles.contactsBlock}>
          <Section>
            <a
              className={` ${styles.link} ${styles.tel}`}
              href="tel:063-128-4609"
            >
              (063) 128-46-09
            </a>
            <a className={styles.link} href="mailto:bubu.shop2018@gmail.com">
              bubu.shop2018@gmail.com
            </a>
            <a
              className={styles.link}
              href="https://www.google.com.ua/maps"
              target="_blank"
              rel="noreferrer"
            >
              Одеса, вул. Михайлівська 8 (щодня з 10:00 до 20:00)
            </a>
            <SocialNetworksList />
          </Section>
        </address>

        <div className={styles.infoBlock}>
          <InfoBlock />
        </div>
      </div>

      <div className={styles.addInfoBlock}>
        <Section className={styles}>
          <p className={styles.copyright}>© Bubu 2022. Всі права захищені.</p>
          <ul className={styles.paymentSystems}>
            <li className={styles.itemImg}>
              <img className={styles.img} src={masterCard} alt="" />
            </li>
            <li className={styles.itemImg}>
              <img className={styles.img} src={liqPay} alt="" />
            </li>
            <li className={styles.itemImg}>
              <img className={styles.img} src={visa} alt="" />
            </li>
          </ul>
        </Section>
      </div>
    </div>
  );
};

export default Footer;
