import { NavLink } from "react-router-dom";

import styles from "./navBar.module.scss";

const NavBar = () => {
  return (
    <nav className={styles.navbar}>
      <ul className={styles.ulNavbar}>
        <li className={styles.item}>
          <NavLink className={styles.link}>Про нас</NavLink>
        </li>
        <li className={styles.item}>
          <NavLink className={styles.link}>Контакти</NavLink>
        </li>
        <li className={styles.item}>
          <NavLink className={styles.link}>Доставка і оплата</NavLink>
        </li>
        <li className={styles.item}>
          <NavLink className={styles.link}>Повернення і обмін</NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
