import { NavLink } from "react-router-dom";
import { nanoid } from "nanoid";

import HeaderBtns from "../HeaderBtns/HeaderBtns";
import NavBar from "../NavBar/NavBar";
import ContactsBlock from "../ContactsBlock/ContactsBlock";
import { ReactComponent as DesktopMenuIcon } from "../../assets/images/icon/desktopMenu.svg";
import { ReactComponent as LogoIcon } from "../../assets/images/icon/logo.svg";
import { ReactComponent as BurgerMenu } from "../../assets/images/icon/menu.svg";
import { ReactComponent as SearchIcon } from "../../assets/images/icon/inputSearch.svg";

import styles from "./header.module.scss";

const Header = () => {
  const id = nanoid();
  return (
    <div className={styles.header}>
      <div className={styles.container}>
        <div className={styles.upContainer}>
          <div className={styles.leftContainer}>
            <BurgerMenu className={styles.burgerMenu} />
            <NavLink to={"/"}>
              <LogoIcon className={styles.logo} />
            </NavLink>
            <div className={styles.navbar}>
              <NavBar />
            </div>
          </div>
          <div className={styles.rightContainer}>
            <ContactsBlock />
          </div>
        </div>

        <div className={styles.catalogContainer}>
          <button className={styles.menuIcon} type="button">
            <DesktopMenuIcon />
          </button>
          <span className={styles.text}>Каталог</span>
        </div>

        <div className={styles.downContainer}>
          <div className={styles.inputContainer}>
            <input id={id} className={styles.input} placeholder="Я шукаю..." />
            <button type="button" className={styles.srchBtn}>
              <SearchIcon />
            </button>
          </div>
          <HeaderBtns />
        </div>
      </div>
    </div>
  );
};

export default Header;
