import { NavLink } from "react-router-dom";

import SocialNetworksList from "../SocialNetworksList/SocialNetworksList";
import { ReactComponent as UserIcon } from "../../assets/images/icon/user.svg";

import styles from "./contactsBlock.module.scss";

const ContactsBlock = () => {
  return (
    <div className={styles.contactList}>
      <a className={styles.telLink} href="tel:063-128-4609">
        (063) 128-46-09
      </a>
      <div className={styles.socialNetworksList}>
        <SocialNetworksList />
      </div>
      <div className={styles.langBlock}>
        <button className={`${styles.langBtn} ${styles.ukr}`}>Укр</button>
        <button className={styles.langBtn}>Eng</button>
      </div>
      <div className={styles.userBlock}>
        <NavLink to={"/login"} className={styles.link}>
          <UserIcon className={styles.icon} />
          <p className={styles.text}>Вхід</p>
        </NavLink>
      </div>
    </div>
  );
};

export default ContactsBlock;
