import { NavLink } from "react-router-dom";

import Section from "../Section/Section";
import WhiteTitle from "../Title/WhiteTitle";

import styles from "./infoBlock.module.scss";

const InfoBlock = () => {
  return (
    <Section>
      <div className={styles.lists}>
        <div className={styles.infoWrappper}>
          <WhiteTitle text="Інформація" />
          <ul className={styles.infoList}>
            <li className={styles.item}>
              <NavLink className={styles.link}>Про нас</NavLink>
            </li>
            <li className={styles.item}>
              <NavLink className={styles.link}>Контакти</NavLink>
            </li>
            <li className={styles.item}>
              <NavLink className={styles.link}>Доставка і оплата</NavLink>
            </li>
            <li className={styles.item}>
              <NavLink className={styles.link}>Повернення та обмін</NavLink>
            </li>
            <li className={styles.item}>
              <NavLink className={styles.link}>
                Політика конфеденційності
              </NavLink>
            </li>
          </ul>
        </div>

        <div>
          <WhiteTitle text="Каталог" />
          <div className={styles.catalogLists}>
            <ul className={styles.infoList}>
              <li className={styles.item}>
                <NavLink className={styles.link}>Дитячі коляски</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Дитяча кімната</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Стільці і шезлонги</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Все для годування</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Гігієна та догляд</NavLink>
              </li>
            </ul>

            <ul className={styles.infoList}>
              <li className={styles.item}>
                <NavLink className={styles.link}>Автокрісла</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Дитячий транспорт</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Іграшки</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Дитячий одяг</NavLink>
              </li>
              <li className={styles.item}>
                <NavLink className={styles.link}>Новий товар</NavLink>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </Section>
  );
};

export default InfoBlock;
